<template>
  <div class="container">
    <div class="arrow"></div>
    <div class="roulette">
      <!-- fill color -->
      <div class="fill fill_1">
        <div class="content">{{ ROULETTE_DATA[0] }}</div>
      </div>
      <div class="fill fill_2">
        <div class="content">{{ ROULETTE_DATA[1] }}</div>
      </div>
      <div class="fill fill_3">
        <div class="content">{{ ROULETTE_DATA[2] }}</div>
      </div>
      <div class="fill fill_4">
        <div class="content">{{ ROULETTE_DATA[3] }}</div>
      </div>
      <div class="fill fill_5">
        <div class="content">{{ ROULETTE_DATA[4] }}</div>
      </div>
      <div class="fill fill_6">
        <div class="content">{{ ROULETTE_DATA[5] }}</div>
      </div>
      <div class="fill fill_7">
        <div class="content">{{ ROULETTE_DATA[6] }}</div>
      </div>
      <div class="fill fill_8">
        <div class="content">{{ ROULETTE_DATA[7] }}</div>
      </div>
      <!-- line -->
      <div class="line line_1"></div>
      <div class="line line_2"></div>
      <div class="line line_3"></div>
      <div class="line line_4"></div>
    </div>
    <div v-if="$store.state.step == 0">
      <button class="trigger" @click="onClickTigger">
        메뉴를 선택해주세요!
      </button>
    </div>
    <div v-else-if="$store.state.step <= 7">
      <button class="trigger" @click="onClickTigger">
        {{ $store.state.menuKor }} 먹을래요!
      </button>
      <br />
      <button class="trigger">
        <router-link to="/camera">메뉴를 스캔할게요!</router-link>
      </button>
      <br />
      <button class="trigger">
        <router-link to="/easy-camera">메뉴를 스캔할게요!?</router-link>
      </button>
    </div>
    <div class="pd-sm" v-if="$store.state.step == 7">
      <input
        class="input is-info is-small"
        placeholder="메뉴를 기입해주세요"
        v-model="userInput"
        ref="menuFocus"
      />
      <button
        class="button is-small is-primary is-outlined"
        @click="addUserInput"
      >
        <i class="fas fa-plus"></i>
      </button>
      <button
        class="button is-small is-info is-outlined"
        @click="clearUserInput"
      >
        <i class="fas fa-redo"></i>
      </button>

      <!-- <input :value="imeInput" @input="imeUpdate" /> -->
      <!-- <p>{{ $store.state.setMenu }}</p> -->
      <!-- <button @click="addImeInput">메뉴 추가하기</button> -->
      <!-- <p>{{ $store.state.setMenu }}</p> -->
    </div>
  </div>
</template>

<script>
export default {
  name: "Roulette",

  data() {
    return {
      ROULETTE_DATA: [],
      imeInput: [],
    };
  },

  updated() {
    this.ROULETTE_DATA = this.$store.state.setMenu;
  },
  computed: {
    userInput: {
      get() {
        return this.$store.state.userInput;
      },
      set(userValue) {
        this.$store.commit("updateUserInput", userValue);
      },
    },
  },

  methods: {
    clearUserInput() {
      this.$store.state.menuData[0].custom = [];
      this.$store.state.setMenu = [];
      this.ROULETTE_DATA = [];
      this.$refs.menuFocus.focus();
      console.log(this.$store.state.menuData[0].custom);
      console.log(this.$store.state.setMenu);
    },
    addUserInput() {
      this.$store.state.setMenu.push(this.userInput);
      /* Shuffle을 위해 */
      this.$store.state.menuData[0].custom = this.$store.state.setMenu;
      this.userInput = "";
      this.$refs.menuFocus.focus();
      console.log(this.$store.state.menuData[0].custom);
      console.log(this.$store.state.setMenu);
    },
    onClickTigger() {
      const loopAnimationOptions = {
        fill: "forwards",
        duration: 2000,
        easing: "ease-in-out",
      };
      const roulette = document.querySelector(".roulette");
      const selectedIdx = Math.floor(Math.random() * 7);
      const additionalDeg = 22.5 * (2 * (1 - selectedIdx) + 1);
      const totalDeg = 7200 + additionalDeg;
      const loopAnimation = [
        { transform: "rotate(0deg)" },
        { transform: `rotate(${totalDeg}deg)` },
      ];
      roulette.animate(loopAnimation, loopAnimationOptions);
    },
    // imeUpdate(e) {
    //   this.imeInput = e.target.value;
    // },
    // addImeInput() {
    //   this.$store.state.menuData[0].custom.push(this.imeInput);
    //   console.log(this.$store.state.menuData[0].custom);
    //   this.imeInput = "";
    // },
  },
};
</script>

