import { createStore } from 'vuex';
import MenuData from './components/MenuData.js'

const store = createStore({
	state() {
		return {
			menuData: MenuData,
			setMenu: "",
			step: 0,
			menuKor: ""
		}
	},
	mutations: {
		updateUserInput(state, userInput) {
			state.userInput = userInput
		},

		setRice() {
			this.state.setMenu = []
			this.state.setMenu = this.state.menuData[0].rice.sort(() => 0.5 - Math.random())
			this.state.step = 1
			this.state.menuKor = "밥"

		},
		setNoodle() {
			this.state.setMenu = []
			this.state.setMenu = this.state.menuData[0].noodle.sort(() => 0.5 - Math.random())
			// console.log(this.state.setMenu)
			this.state.step = 2
			this.state.menuKor = "면"
		},
		setSoup() {
			this.state.setMenu = []
			this.state.setMenu = this.state.menuData[0].soup.sort(() => 0.5 - Math.random())
			this.state.step = 3
			this.state.menuKor = "국"
		},
		setJapvet() {
			this.state.setMenu = []
			this.state.setMenu = this.state.menuData[0].japvet.sort(() => 0.5 - Math.random())
			this.state.step = 4
			this.state.menuKor = "일식&베트남식"
		},
		setDessert() {
			this.state.setMenu = []
			this.state.setMenu = this.state.menuData[0].dessert.sort(() => 0.5 - Math.random())
			this.state.step = 5
			this.state.menuKor = "디저트"
		},
		setMeatchicken() {
			this.state.setMenu = []
			this.state.setMenu = this.state.menuData[0].meatchicken.sort(() => 0.5 - Math.random())
			this.state.step = 6
			this.state.menuKor = "고기&치킨"
		},
		setCustom() {
			this.state.setMenu = []
			this.state.setMenu = this.state.menuData[0].custom.sort(() => 0.5 - Math.random())
			this.state.step = 7
			this.state.menuKor = "직접 골라서"
		}
	}
})
export default store